/****** Main Styling ******/

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  background-color: #beeee7;
}

header {
  color: #00bcd4;
  height: 56px;
  margin: 0 0 14px 0;
  text-align: center;
}

p {
  margin: 8px 0 0 0;
}

.container p {
  text-align: center;
  padding: 0;
}

/****** Header Styling ******/

header p {
  margin: 16px 0 5px 0;
}

.menu {
  text-decoration: none;
  padding: 6px;
  margin: 0;
  color: #727272;
}

.menu:visited {
  color: #727272;
}

.menu:hover {
  color: #ffa000;
}

.menu:active {
  color: #ff630d;
}

header a ~ p {
  margin: 0;
  padding: 0;
  display: inline;
  color: #eceff1;
}

#display-name {
  font-weight: 400;
}

/****** Login Styling ******/

.login {
  margin: 86px auto 0 auto;
  text-align: center;
}

#login-btn {
  width: 225px;
  padding: 7px 5px;
}

.btns {
  
}

.btn p {
  margin: 8px 0 0 0;
  padding: 0;
}

.btn > img {
  float: left;
  margin-left: 10px;
}

.btn-container {
  /* Styling for the div that contains the buttons */
  margin: -10px auto 0 auto;
  text-align: center;
}

.btn {
  /* Styling for buttons */
  margin: 0 8px;
  color: white;
  background-color: #00bcd4;
  display: inline-block;
  border: 0;
  font-size: 14px;
  border-radius: 3px;
  padding: 10px 5px;
  width: 100px;
  font-weight: 500;
}

.btn:focus {
  /* Remove outline when hovering over button */
  outline: none;
}

.btn:active {
  /* Scale the button down by 10% when clicking on button */
  -ms-transform: scale(0.9, 0.9);
      transform: scale(0.9, 0.9);
  -webkit-transform: scale(0.9, 0.9);
  -moz-transform: scale(0.9, 0.9);
}

.btn-delete {
  /* Styling for delete button */
  background-color: #eceff1;
  color: #212121;
}

#click-nbr {
  font-weight: 400;
}

/****** Profile Styling ******/

.github-profile {
  width: 350px;
  height: 200px;
  border-radius: 3px;
  margin: 86px auto 0 auto;
  background-color: #eee;
  text-align: center;
  color: #000;
}

.github-profile p:first-child {
  padding-top: 16px;
}

.github-profile p:nth-child(5) {
  margin-bottom: 16px;
}

.github-profile p {
  margin: 0 0 0 16px;
  text-align: left;
}

.profile-value {
  font-weight: 400;
}

span {
  font-weight: 500;
}

.github-profile > img {
  padding-top: 16px;
  margin-bottom: 16px;
}

.github-profile .menu:hover {
  color: #ffa000;
}

.github-profile .menu:active {
  color: #ff630d;
}

#artistDiscussion {
  /* width: 700px; */
  height: 300px;
}

#menu-divide {
  color: #ffffff;
  display: inline;
  margin: 0;
}

.toon-container{
  /* width: 50%; */
  margin: 0 auto;
}

#toons{
